	export default {
		data() {
			return {
        // 判断是否是会员 1 是会员 0 不是会员
        isBox: true,
        name: '',
        etime: '',
        image_url: '',
        vipList: [],
        user_id: ''
			}
    },
    onLoad() {
      this.user_id = uni.getStorageSync('user_id')
      // 查询会员信息接口
      this.getVip()
      // 查询所有的套餐信息接口
      this.getFindList()
      // 查询是否是会员接口
      this.isMember()
    },
		methods: {
      // 查询会员信息接口
      getVip() {
        this.$http
          .get("/memberPackage/getVip", { params: { user_id: this.user_id } })
          .then(res => {
            console.log(res)
            this.name = res.data.name
            this.etime = res.data.etime
            this.image_url = res.data.image_url
          });
      },
      // 查询所有的套餐信息接口
        getFindList() {
          this.$http
            .get("/memberPackage/findList", {})
            .then(res => {
              this.vipList = res.data
            });
        },
        // 查询是否是会员接口
        isMember() {
          this.$http.get('/user/isMember', { params: { user_id: this.user_id } }).then(res => {
            console.log(res)
            if (res.data == 0) {
              this.isBox = false
            } else {
              this.isBox = true
            }
          })
        },
      // 去会员赠送页面
			goVipGive() {
        if (this.member == 0) {
          uni.navigateTo({
            url: '/pages/vipGive/vipGive'
          })
        } else {
          uni.showToast({
              title: '敬请期待',
              duration: 2000,
              icon: 'none'
          });
          // uni.showToast({
          //     title: '您还不是会员 无法参与',
          //     duration: 2000,
          //     icon: 'none'
          // });
        }
      },
      // 去会员管理页面
      goVipManage() {
        uni.navigateTo({
					url: '/pages/vipManage/vipManage'
				});
      },
      // 去推荐有礼页面
      goMineRecommendCourtesy() {
        uni.showToast({
            title: '敬请期待',
            duration: 2000,
            icon: 'none'
        });
        // if (this.member == 1) {
        //   uni.navigateTo({
        //     url: '/pages/mineRecommendCourtesy/mineRecommendCourtesy'
        //   })
        // } else {
        //   uni.showToast({
        //       title: '您还不是会员 无法参与',
        //       duration: 2000,
        //       icon: 'none'
        //   });
        // }
      },
      // 去会员套餐详情页面
      goVipGoodsDetailsInfo(id) {
        uni.navigateTo({
					url: '/pages/vipGoodsDetailsInfo/vipGoodsDetailsInfo?id=' + id
				});
      },
      // 去会员协议页面
      goVipMemberAgreement() {
        uni.navigateTo({
					url: '/pages/vipMemberAgreement/vipMemberAgreement'
				});
      },
      checkboxChange(e) {
        console.log(e.detail.value)
      }
		}
	}
